.pj1-section {
  width: 100vw;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: none;
  position: relative;
}

.game-shadow-bg {
  background: none !important;
}
.pj1-inner {
  display: flex;
  width: 1060px;
  height: 900px;
  background: rgba(251, 240, 240, 1); /* 통일! */
  border-radius: 22px; /* MainPage 카드와 동일 */
  box-shadow: 0 7px 36px 0 #cfcbe9a0, 0 0 0 4px #ded6f6; /* 통일! */
  border: 3px solid #dbc6f2; /* 통일! */
  position: relative;
  overflow: hidden;
  padding: 36px 55px 36px 48px;
  transition: box-shadow 0.17s;
}
.game-border {
  box-shadow: 0 0 0 7px #be65f2, 0 7px 36px #c54af745;
  border: 4.4px solid #c3b1f5;
}

.pj1-back-btn {
  position: absolute;
  width: 190px;
  height: 60px;
  left: 100px;
  top: 160px;
  z-index: 11;
  background: #26203a;
  color: #ffc0fa;
  font-size: 18px;
  font-family: 'Press Start 2P', 'Do Hyeon', 'Urbanist', sans-serif;
  font-weight: bold;
  border: 2px solid #fae1ff;
  border-radius: 12px;
  padding: 13px 34px 13px 22px;
  cursor: pointer;
  box-shadow: 0 3px 16px #ce5ff36a, 0 0 0 4px #fff2;
  letter-spacing: 0.01em;
  transition: all 0.17s cubic-bezier(.68,-0.55,.27,1.55);
}
.pj1-back-btn:hover {
  background: #f3d4ff;
  color: #6e18ac;
  border-color: #f8baff;
}

.pj1-left {
  flex: 0 0 430px;
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0;
  z-index: 1;
}

.pj1-img-frame {
  width: 410px;
  height: 530px;
  overflow: hidden;
  border-radius: 23px;
  background: #20132c;
  box-shadow: 0 6px 28px #c9bfe4bb, 0 0 0 3px #ecd7f6;
  border: 5px solid #e295f7;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  position: relative;
}
.game-glow {
  box-shadow: 0 0 25px #bc69f9, 0 0 0 9px #eebdff80, 0 12px 48px #65007520;
}

.pj1-img-scroll {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  scrollbar-width: none;
  -ms-overflow-style: none;
}
.pj1-img-scroll::-webkit-scrollbar { display: none; }

.pj1-img {
  width: 100%;
  height: auto;
  min-height: 830px;
  user-select: none;
  pointer-events: none;
  background: #fff;
  border-radius: 0 0 18px 18px;
  box-shadow: 0 2px 18px #fff6, 0 0 0 6px #ebc6ff30;
}

.pj1-right {
  flex: 1 1 0%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 18px 0 0 74px;
}

.pj1-label {
  font-size: 1.12rem;
  font-family: 'Press Start 2P', 'Do Hyeon', sans-serif;
  font-weight: 700;
  color: #c797f5;
  margin-bottom: 30px;
  letter-spacing: 0.03em;
  text-shadow: 0 1px 8px #e6d2ff7d;
}

.pj1-title {
  font-size: 2.45rem;
  font-weight: 900;
  letter-spacing: 0.05em;
  color: #a835cf;
  margin-bottom: 30px;
  font-family: 'Do Hyeon', 'Urbanist', 'Press Start 2P', sans-serif;
  text-shadow: 0 1px 12px #fff8, 0 2px 12px #d995fd2c;
}

.pj1-grad {
  background: linear-gradient(90deg, #ffb7e7, #c0a7fc 60%, #b2ebff 100%);
  color: #7b338b;
  padding: 2px 11px 2px 9px;
  border-radius: 10px;
  font-size: 1.02rem;
  margin-bottom: 13px;
  display: inline-block;
  box-shadow: 0 1.5px 8px #fff2, 0 0 0 4px #f2d9ff29;
}

.pj1-desc {
  font-family: 'Pretendard' ;
  font-size: 1.11rem;
  color: #784572;
  margin-bottom: 160px;
  line-height: 1.8;
  letter-spacing: 0.01em;
}

.pj1-tags {
  margin-bottom: 36px;
}
.pj1-tags span {
  display: inline-block;
  background: #f7d9fe;
  color: #9353a6;
  font-weight: 600;
  font-size: 1.01rem;
  border-radius: 8px;
  padding: 3.5px 14px;
  margin-right: 8px;
  margin-bottom: 6px;
  letter-spacing: 0.01em;
  font-family: 'Pretendard', sans-serif;
  box-shadow: 0 1px 6px #fff1;
}

.pj1-btns {
  display: flex;
  gap: 22px;
  margin-bottom: 9px;
}
.pj1-btn {
  display: inline-block;
  background: #e6c7fa;
  color: #792bb0;
  font-weight: bold;
  font-size: 1.13rem;
  border-radius: 12px;
  border: 2.5px solid #edbaff;
  padding: 11px 38px;
  text-decoration: none;
  transition: 0.13s cubic-bezier(.68,-0.55,.27,1.55);
  font-family: 'Press Start 2P', sans-serif;
  box-shadow: 0 2px 9px #e9bbff3d;
}
.pj1-btn:hover {
  background: #ffd7fb;
  color: #191022;
  border-color: #c795e8;
  transform: scale(1.09) rotate(-2deg);
}
