.press-start-container {
  width: 100%;
  height: 100vh;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.press-logo-img {
  position: absolute;
  bottom: 19%;  /* 원하는 위치로 조절 */
  left: 50%;
  transform: translateX(-50%);
  width: 100%; /* 필요시 크기 조절 */
  z-index: 2;
  animation: slideDown 0.8s cubic-bezier(.77,0,.18,1) 0.15s both,
  logo-glitch 1.7s infinite ;
  filter: drop-shadow(0 0 8px #DD8290)
          drop-shadow(0 0 16px #FFBFA2)
          drop-shadow(0 0 24px #ea7fff);
          

}

/* 기본 텍스트 스타일 */
.press-start-text {
  font-family: "Press Start 2P", monospace;
  font-size: 2.7rem;
  color: #fff;
  cursor: pointer;
  letter-spacing: 2px;
  top: 31%;
  user-select: none;
  position: relative;     
  transition: none;
}

/* 글리치 효과 (hover) */
.press-start-text:hover {
  animation: glitch-video 0.75s infinite steps(1, end);
}


@keyframes glitch-video {
  0% { text-shadow: 2px 0 yellow, -2px 0 black; }
  10% { text-shadow: -2px 0 red, 2px 0 blue; }
  15% { text-shadow: 1px 0 lawngreen, -3px 0 white;}
  20% { text-shadow: 2px 1px black, -2px -1px blue; }
  30% { text-shadow: -1px -2px red, 1px 2px pink; }
  40% { text-shadow: 2px 2px black, -2px -2px blue; }
  50%, 100% { text-shadow: 0 0 transparent; }
}

@keyframes slideDown {
  0% { opacity: 0; transform: translate(-50%, -100px);}
  100% { opacity: 1; transform: translate(-50%, 0);}
}

@keyframes slideUp {
  0% { opacity: 0; transform: translateY(100px);}
  100% { opacity: 1; transform: translateY(0);}
}

@keyframes logo-glitch {
 0%   { filter: drop-shadow(2px 0 #d9bfff) drop-shadow(-2px 0 #ffa4ff); }
  10%  { filter: drop-shadow(-2px 0 #a084ee) drop-shadow(2px 0 #e88bfa); }
  15%  { filter: drop-shadow(1px 0 #d392e1) drop-shadow(-3px 0 #FFC39F); }
  20%  { filter: drop-shadow(2px 1px #bfa4ff) drop-shadow(-2px -1px #ffb1db); }
  30%  { filter: drop-shadow(-1px -2px #e1bfff) drop-shadow(1px 2px #f2b5f5); }
  40%  { filter: drop-shadow(2px 2px #ffb1db) drop-shadow(-2px -2px #a084ee); }
  50%,100% { filter: none; }
}