@import url('https://cdn.jsdelivr.net/gh/orioncactus/pretendard/dist/web/variable/pretendardvariable.css');

@font-face {
    font-family: 'Pretendard-Regular';
    src: url('https://fastly.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
    font-weight: 400;
    font-style: normal;
}

body {
  font-family: 'Pretendard', 'Do Hyeon', 'Urbanist', sans-serif;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

.main-content {
  width: 100vw;
  min-height: 900px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-bottom: 80px;
}

.tab-wrap {
  display: flex;
  margin-bottom: -10px;
  margin-right: 715px;
  gap: 10px;
}
.tab-wrap button {
  font-family: 'DungGeunMo', 'Pretendard', sans-serif;
  font-size: 28px;
  padding: 7px 26px;
  border-radius: 8px 8px 0 0;
  border: none;
  background: #f7f7fc;
  color: #a835cf;
  cursor: pointer;
  outline: none;
  margin-right: 5px;
  box-shadow: 0 1px 6px #d9d2f55a;
  border-bottom: 2.7px solid #ded6f6;
  transition: 0.15s;
}
.tab-wrap button.active {
  background: #ebeaf4;
  color: #7d39bb;
  border-bottom: 3px solid #a835cf;
  font-weight: bold;
}

.profile-card, .skill-card {
  background: rgba(251, 240, 240, 0.85);
  border-radius: 22px;
  margin-top: 10px;
  padding: 34px 46px 38px 46px;
  box-shadow: 0 7px 36px 0 #cfcbe9a0, 0 0 0 4px #ded6f6;
  border: 3px solid #dbc6f2;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  width: 1060px;
  height: 600px;
  gap: 10px;
}

.profile-wrap {
  width: 100%;
}

.profile-top {
  display: flex;
  align-items: center;
  gap: 33px;
  justify-content: start;
}

.profile-img {
  width: 280px;
  height: 340px;
  border-radius: 14px;
  object-fit: cover;
  margin: 0 0 10px 0;
  box-shadow: 0 3px 20px #b89af878, 0 0 0 2.5px #e3cdf680;
  border: 2.3px solid #e1d2ff;
}

.profile-info {
  flex: 1;
}

.profile-name {
  font-family:  'DungGeunMo','Pretendard',sans-serif;
  font-size: 50px;
  font-weight: 700;
  color: #a374db;
  margin-bottom: 50px;
}

.profile-detail {
  margin: 70px 0 15px 0;
  font-size: 27px;
  font-family: 'DungGeunMo','Pretendard',sans-serif;
  color: #a374db;
  letter-spacing: 1px;
}

.profile-d-row {
  margin-top: 50px;
}

.profile-bottom {
  margin-top: 70px;
  padding-left: 8px;
}

.label {
  font-family: 'Press Start 2P', sans-serif;
  font-size: 15px;
}

.profile-row {
  font-family: 'DungGeunMo','Pretendard',sans-serif;
  font-size: 25px;
  color: #a374db;
  margin-bottom: 40px;
}

.skill-row {
  display: grid;
  grid-template-columns: repeat(4, 200px);
  grid-template-rows: repeat(2, 200px);
  gap: 28px 36px;
  margin: 85px 75px;
}

.skill-box {
  width: 200px;
  height: 200px;
  border-radius: 18px;
  background: #edeaf9;
  border: 2.7px solid #dbc6f2;
  box-shadow: 0 4px 24px #e3dafeb3, 0 0 0 2.5px #e3cdf672;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: box-shadow 0.21s, transform 0.21s, border 0.2s;
  cursor: pointer;
  position: relative;
}
.skill-box.active {
  background: #ece7fa;
  border-color: #ba95ed;
  box-shadow: 0 0 0 7px #be65f233, 0 12px 38px #c54af745;
  transform: translateY(-8px) scale(1.03);
}

.skill-img-bg img {
  width: 110px;
  height: 110px;
  object-fit: contain;
  user-select: none;
  pointer-events: none;
}

.skill-progress-bg {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.circle-progress text {
  fill: #9b61d9 !important;
  font-size: 19px;
  font-family: 'press start 2p', sans-serif;
}
