.circle-nav-vertical {
  position: fixed;
  right: 1.5vw;
  top: 46vh;
  display: flex;
  flex-direction: column;
  gap: 24px;
  z-index: 999;
}

.circle-nav-btn {
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 0;
  transition: filter 0.1s;
}

.circle-dot {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  font-family: "Press Start 2P", monospace;
  font-size: 1.2rem;
  color: #fff;
  background: repeating-linear-gradient(135deg, #d4c4f9 0 2px, #a99ac4 2px 6px);
  border: 3px solid #fff;
  border-radius: 8px; /* 각진 픽셀 원 느낌 */
  box-shadow: 0 0 0 2px #6a5c94;
  image-rendering: pixelated;
  /* 픽셀 원 느낌 내기 위해 border-radius를 8~10px 정도만 */
}

.circle-nav-btn.active .circle-dot {
  background: #fff;
  color: #a99ac4;
  border: 3px solid #a99ac4;
  box-shadow: 0 0 0 2px #fff;
}

.circle-nav-btn:hover .circle-dot {
  filter: brightness(1.2) contrast(1.2);
}