.linkstart-bg {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  position: relative;
}

.linkstart-text {
  font-family: "Press Start 2P", monospace;
  font-size: 4.5rem;
  color: #fff;
  letter-spacing: 2px;
  opacity: 0;
  transform: scale(0.7);
  filter: blur(12px) brightness(1.2);
  transition: none;
  will-change: transform, opacity, filter;
  cursor: pointer;
  user-select: none;
}

/* 팡! 등장 */
.linkstart-text.pop {
  animation: linkstart-pop 0.28s cubic-bezier(.4,2,.5,1.05) forwards,tapHintFade 0.6s infinite alternate;
}
@keyframes linkstart-pop {
  0% {
    opacity: 0;
    transform: scale(0.7);
    filter: blur(12px) brightness(1.2);
    letter-spacing: 24px;
  }
  65% {
    opacity: 1;
    transform: scale(1.25);
    filter: blur(2px) brightness(2.5);
    letter-spacing: 6px;
  }
  100% {
    opacity: 1;
    transform: scale(1.0);
    filter: blur(0) brightness(1.1);
    letter-spacing: 2px;
  }
}

/* 슈욱! 줄어들며 사라짐 */
.linkstart-text.zoom {
  animation: linkstart-zoom 0.58s cubic-bezier(.57,.01,.99,.52) forwards;
}
@keyframes linkstart-zoom {
  0% {
    opacity: 1;
    transform: scale(1.0);
    filter: blur(0) brightness(1.1);
  }
  70% {
    opacity: 1;
    transform: scale(16.5);
    filter: blur(8px) brightness(3.2);
  }
  100% {
    opacity: 0;
    transform: scale(30);
    filter: blur(22px) brightness(8.5);
  }
}

/* "Click!" 표시 */
.linkstart-tap {
  position: absolute;
  left: 50%; top: 70%;
  transform: translate(-50%, 0);
  color: #ffe0ef;
  font-family: "Press Start 2P", monospace;
  font-size: 1.6rem;
  opacity: 0.75;
  pointer-events: none;
}
@keyframes tapHintFade {
  0% { opacity: 1.8; }
  70% {text-shadow:  #ffe0ef;}
  100% { opacity: 0.25; }
}