@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

.selectuser-bg {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
}


.selectuser-title {
  position: absolute;
  top: 100px; /* 원하는 위치로 조정 (사진 기준) */
  left: 50%;
  transform: translateX(-50%);
  width: 600px;
  height: 65px;
  background: rgba(255,255,255,0.35);
  border: 4px solid #4b395b;
  border-radius: 12px;
  font-family: 'Press Start 2P', monospace;
  font-size: 1.25rem;
  color: #4b395b;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2;
  letter-spacing: 0.04em;
  font-weight: bold;
  box-shadow: 0 6px 24px #bb9cf4a8;
}
.selectuser-row {
  display: flex;
  gap: 4.9em;
}
.selectuser-slot {
  width: 250px;
  height: 400px;
  background: #e8e3f1;
  border: 3px solid #fff;
  padding: 1.1em 0.7em 0.7em 0.7em;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  transition: border 0.2s, box-shadow 0.2s, transform 0.22s;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
}
.selectuser-slot.selected {
  background-color: #fff3fc;
  border: 3px solid #bb9cf4;
  box-shadow: 0 0 18px #bb9cf4;
  transform: scale(1.08);
}
.selectuser-arrow {
  position: absolute;
  top: -1.4em;
  left: 50%;
  transform: translateX(-50%);
  color: #bb9cf4;
  font-size: 1.4rem;
  font-family: 'Press Start 2P', monospace;
  pointer-events: none;
}
.selectuser-icon {
  font-size: 3.3rem;
  margin-bottom: 0.8em;
  margin-top: 0.4em;
  font-family: 'Press Start 2P', monospace;
  color: #fff;
}
.pixel-q {
  font-size: 3.5rem;
  font-family: 'Press Start 2P', monospace;
  color: #bb9cf4;
}
.selectuser-info {
  width: 90%;
  background: #faf7ff;
  color: #4b395b;
  font-family: 'Press Start 2P', monospace;
  font-size: 0.95rem;
  line-height: 1.8;
  margin-bottom: 1.1em;
  text-align: left;
  border: 2px solid #bb9cf4;
  padding: 0.6em 0.5em 0.4em 1em;
  margin-left: auto;
  margin-right: auto;
}
.selectuser-start {
  width: 100%;
  height: 80px;
  margin: 0.7em auto 0.25em auto;
  background: #bb9cf4;
  color: #fff;
  font-family: 'Press Start 2P', monospace;
  font-size: 1.2rem;
  border: none;
  padding: 0.5em 0;
  cursor: pointer;
  transition: background 0.16s, color 0.13s;
  outline: none;
}
.selectuser-start.focused,
.selectuser-slot.selected .selectuser-start {
  background: #d3c5ec;
  color: #a99ac4;
}
