@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

.contact-arcade-bg {
  width: 100vw;
  height: 100vh;
  background: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
}

.contact-arcade-frame {
  background: rgba(169,154,196,0.93);
  border: 5px solid #fff;
  border-radius: 22px;
  padding: 38px 28px 32px 28px;
  box-shadow: 0 0 60px 10px #bb9cf4bb, 0 0 0 12px #fff8, 0 0 0 22px #bb9cf466;
  min-width: 970px; max-width: 1200px;
  display: flex; flex-direction: column;
  align-items: center; gap: 24px;
  position: relative;
}

.contact-arcade-title {
  font-family: 'Press Start 2P', monospace;
  font-size: 2.7rem;
  color: #fff;
  letter-spacing: 0.10em;
  text-align: center;
  margin-bottom: 14px;
  text-shadow: 0 4px 20px #bb9cf4, 0 0 10px #fff6;
  filter: drop-shadow(0 0 10px #c6b1ee);
}

.contact-neon-glow {
  color: #fff;
  text-shadow:
    0 0 8px #bb9cf4,
    0 0 24px #FFD6F2,
    0 0 2px #fff,
    0 2px 12px #bb9cf4;
}

.contact-row {
  width: 100%;
  display: flex;
  justify-content: space-between;
  gap: 34px;
  margin-bottom: 16px;
}

.contact-card.pixel-card {
  background: #fff9fe;
  border: 3.5px solid #b699ea;
  border-radius: 13px;
  box-shadow: 0 0 15px 3px #bb9cf477, 0 2.5px 0 #ffd6f2 inset;
  width: 270px; min-height: 130px;
  display: flex; flex-direction: column;
  align-items: center; justify-content: center;
  font-family: 'Press Start 2P', monospace;
  padding: 20px 14px 15px 14px;
  transition: transform 0.16s;
}
.contact-card.pixel-card:hover {
  transform: scale(1.035) rotate(-1deg);
  border-color: #fff;
  background: #ffe7fe;
}

.card-title {
  font-size: 1.04rem;
  color: #b17fcf;
  margin-bottom: 12px;
  letter-spacing: 0.06em;
}

.card-content {
  color: #47396d;
  font-family: 'Pretendard';
  font-size: 1.04rem;
  text-align: center;
  line-height: 1.8;
  word-break: break-all;
}
.card-content a {
  color: #b17fcf;
  text-decoration: underline;
  font-weight: bold;
}

.contact-aspire-card {
  width: 86%;
  background: #f6ebff;
  border: 3px dashed #bb9cf4;
  border-radius: 16px;
  padding: 26px 30px 21px 30px;
  font-family: 'Press Start 2P', monospace;
  font-size: 1.05rem;
  color: #47396d;
  margin: 21px 0 8px 0;
  box-shadow: 0 2.5px 0 #ffd6f2 inset;
  text-align: center;
}
.aspire-title {
  font-size: 1.12rem;
  color: #bb9cf4;
  margin-bottom: 18px;
  text-shadow: 0 2px 10px #bb9cf4bb;
}

.aspire-content {
  font-family: 'Pretendard';
}

.contact-design-bar {
  width: 62%;
  height: 38px;
  background: repeating-linear-gradient(
    90deg,
    #d7c2f0 0 24px,
    #a99ac4 24px 48px
  );
  background-size: 100px 100%;
  animation: slide-bar 3s linear infinite;
  border-radius: 11px;
  margin-top: 15px;
  margin-bottom: 2px;
  box-shadow: 0 2.5px 0 #fff8 inset;
  display: flex; align-items: center; justify-content: center;
}
.pixel-bar-text {
  font-family: 'Press Start 2P', monospace;
  font-size: 1.11rem;
  color: #6e588e;
  text-shadow: 0 1px 0 #fff;
  letter-spacing: 0.03em;
}

@keyframes slide-bar {
  0% { background-position-x: 0; }
  100% { background-position-x: 100px; }
}

