.loading-bg {
  width: 100vw;
  height: 100vh;
  background: #222136;    /* 완전 검정 대신 남색-보라 계열 */
  display: flex;
  justify-content: center;
  align-items: center;
}
.loading-window-stack {
  position: relative;
  width: 850px;
  min-height: 510px;
  z-index: 2;
  display: flex;
  justify-content: center;
  align-items: center;
}
.loading-window.layer-1,
.loading-window.layer-2,
.loading-window.layer-3 {
  position: absolute;
  width: 850px;
  min-height: 510px;
  border: 4px solid #F3F4F6;
  background: #A99AC4;
  box-sizing: border-box;
}
.loading-window.layer-1 {
  left: 0;
  top: 0;
  z-index: 3;
}
.loading-window.layer-2 {
  left: 16px;
  top: 18px;
  border-color: #bb9cf4;
  opacity: 0.6;
  z-index: 2;
  filter: blur(0.5px);
}
.loading-window.layer-3 {
  left: 32px;
  top: 36px;
  border-color: #ffc7e6;
  opacity: 0.33;
  z-index: 1;
  filter: blur(0.7px);
}
.loading-contents {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.loading-title {
  width: 100%;
  text-align: left;
  font-family: "Press Start 2P", monospace;
  font-size: 1.24rem;
  color: #F3F4F6;
  letter-spacing: 2px;
  margin-bottom: 24px;
  margin-top: 12px;
  font-weight: bold;
  padding-left: 16px;
  text-shadow: 2px 2px 0 #bb9cf4, 0 2px 0 #ffc7e6;
}
.loading-content {
  width: 100%;
  text-align: center;
  color: #F3F4F6;
  font-family: "Press Start 2P", monospace;
  font-size: 2.7rem;
  letter-spacing: 2.4px;
  margin-bottom: 54px;
  margin-top: 40px;
  text-shadow: 2px 2px 0 #bb9cf4, 0 2px 0 #ffc7e6;
}
.loading-bar-bg {
  width: 80%;
  height: 32px;
  background: repeating-linear-gradient(
    to right, #bb9cf4 0 8px, #222136 8px 12px
  );
  border: 3px solid #F3F4F6;
  margin: 0 auto;
  border-radius: 0;
  overflow: hidden;
  margin-top: 0;
  box-shadow: 0 0 0 2px #bb9cf4;
  position: relative;
}
.loading-bar {
  width: 0;
  height: 100%;
  background: repeating-linear-gradient(
    to right, #F3F4F6 0 8px, transparent 8px 12px
  );
  border-radius: 0;
  transition: width 0.1s;
  image-rendering: pixelated;
}