.projectpage-wrap {
  width: 100%;
  padding: 70px 0 40px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.project-title {
  font-family: 'DungGeunMo', sans-serif;;
  font-size: 60px;
  font-weight: 900;
  margin-bottom: 44px;
  color: #a374db;
  letter-spacing: 0.07em;
}

.project-list {
  display: flex;
  gap: 46px;
  justify-content: center;
}

.project-thumb {
  width: 260px;
  height: 500px;
  background: #f6e6ff;
  border-radius: 12px;
  box-shadow: 0 6px 30px #c8a0ff33;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  transition: 0.18s;
  position: relative;
  border: 2.2px solid #e1a3ff33;
}
.project-thumb:hover {
  transform: translateY(-10px) scale(1.04);
  box-shadow: 0 12px 48px #c69cff44;
  border: 2.2px solid #ea7fff;
}
.project-thumb-img {
  width: 100%;
  height: 72%;
  object-fit: cover;
  border-radius: 12px 12px 0 0;
  margin-bottom: 0;
  background: #fff;
}
.project-thumb-name {
  width: 100%;
  font-family: 'DungGeunMo', sans-serif;;
  font-size: 23px;
  font-weight: bold;
  color: #a374db;
  padding: 20px 0 0 0;
  text-align: center;
  margin: 35px;
  background: none;
  letter-spacing: 0.04em;
}

