.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.bg-video {
  position: fixed;
  width: 100vw;
  height: 100vh;
  object-fit: cover;
  top: 0; left: 0;
  z-index: 0;
}

.content-wrap {
  position: relative;
  z-index: 1;
  width: 100vw;
  /* min-height: 100vh; */
}

.main-section, .project-section, .contact-section {
  width: 100vw;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  /* 위아래 간격은 여기서 padding, margin 등으로 조정 */
}